import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios'; // Axios  API 
import { styles } from './styles'; // styles.tsx   

const SignUpPage: React.FC = () => {
  const navigate = useNavigate();
  const [nickname, setNickname] = useState('');
  const [profileImageUrl, setProfileImageUrl] = useState('');
  const [role, setRole] = useState('');
  const [kakao_Id, setKakao_Id] = useState('');
  const [name, setName] = useState('');
  const [card, setCard] = useState(''); //  
  const [notification_agree, setNotification_Agree] = useState(false); //  

  useEffect(() => {
    // URL jwtToken refreshToken 
    const urlParams = new URLSearchParams(window.location.search);
    const jwtToken = urlParams.get('jwtToken');
    const refreshToken = urlParams.get('refreshToken');

    if (jwtToken && refreshToken) {
      //  localStorage 
      localStorage.setItem('jwtToken', jwtToken);
      localStorage.setItem('refreshToken', refreshToken);

      //     
      const decodedToken = parseJwt(jwtToken);
      setName(decodedToken.name);
      setRole(decodedToken.role);
      setKakao_Id(decodedToken.kakao_Id);
      setProfileImageUrl(decodedToken.profile_image);

      //  URL  , URL  (   URL )
      const newUrl = window.location.origin + window.location.pathname;
      window.history.replaceState(null, '', newUrl);
    }
  }, []);

  function parseJwt(token: string) {
    try {
      const base64Url = token.split('.')[1];
      const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      const jsonPayload = decodeURIComponent(
        atob(base64)
          .split('')
          .map(function (c) {
            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join('')
      );

      return JSON.parse(jsonPayload);
    } catch (error) {
      console.error('    :', error);
      return null;
    }
  }

  const handleSubmit = async () => {
    const memberData = {
      name,
      nickname,
      kakao_Id,
      profileImage: profileImageUrl,
      role,
      card,
      notification_agree
    };

    try {
      const jwtToken = localStorage.getItem('jwtToken'); // JWT    
      const response = await axios.post(
        import.meta.env.VITE_SERVER_URI+'/api/v1/users/kakaologin',
        memberData,
        {
          headers: {
            Authorization: `Bearer ${jwtToken}` // Authorization  JWT  
          }
        }
      );
      console.log('   :', response.data);
      navigate('/'); //    
    } catch (error) {
      console.error('   :', error);
    }
  };

  return (
    <div style={styles.userProfileContainer}>
      <div style={styles.navBar}>
        <button style={styles.navBarButton} onClick={() => navigate(-1)}>
          
        </button>
        <h2>  </h2>
      </div>

      <div style={styles.userInfoSection}>
        <h2> </h2>
        {profileImageUrl && (
          <img
            src={profileImageUrl}
            alt="Profile"
            style={{ width: '150px', height: '150px', borderRadius: '50%' }}
          />
        )}
        <p>
          <strong>:</strong> {name}
        </p>
        <p>
          <strong>:</strong> {role}
        </p>
        <p>
          <strong> ID:</strong> {kakao_Id}
        </p>
      </div>

      <div style={styles.userInfoSection}>
        <h2>  </h2>
        <div style={styles.formGroup}>
          <label style={styles.formGroupLabel}>:</label>
          <input
            type="text"
            style={styles.formGroupInput}
            value={nickname}
            onChange={(e) => setNickname(e.target.value)}
            placeholder=" "
          />
        </div>
        <div style={styles.formGroup}>
          <label style={styles.formGroupLabel}> :</label>
          <input
            type="text"
            style={styles.formGroupInput}
            value={card}
            onChange={(e) => setCard(e.target.value)}
            placeholder="  "
          />
        </div>
        <div style={styles.checkboxGroup}>
          <label>
            <input
              type="checkbox"
              checked={notification_agree}
              onChange={(e) => setNotification_Agree(e.target.checked)}
            />
              
          </label>
        </div>
        <button style={styles.actionButtonSubmit} onClick={handleSubmit}>
           
        </button>
      </div>
    </div>
  );
};

export default SignUpPage;