import React, { useState } from 'react';
import SearchName from '../searchUtils/searchName';
import SearchWord from '../searchUtils/searchWord';
import Button from '@mui/material/Button';

import {
  NameContainer,
  RecentSearchesContainer,
  SearchWordsContainer,
  NoSearchWordsText
} from './styles';

const RecentSearches = () => {
  const initialSearchWords = [
    'React',
    'JavaScript',
    'TypeScript',
    'CSS',
    'HTML',
    'Node.js',
    'Express',
    'MongoDB',
    'GraphQL',
    'Redux',
    'MUI',
    'styled-components',
    'Webpack',
    'Babel',
    'REST API',
    'Axios',
    'Jest',
    'Next.js',
    'Gatsby',
    'ESLint'
  ];

  const [searchWords, setSearchWords] = useState(initialSearchWords);

  const handleDeleteAll = () => {
    setSearchWords([]); // searchWords       
  };

  return (
    <RecentSearchesContainer>
      <NameContainer>
        <SearchName searchName=" " />
        {searchWords.length > 0 && (
          <Button
            variant="contained"
            sx={{
              backgroundColor: '#f2f2f7',
              textAlign: 'center',
              fontSize: '13px',
              fontWeight: 'bold',
              color: 'black',
              padding: '5px',
              borderRadius: '15px',
              boxShadow: 'none',
              '&:hover': {
                backgroundColor: '#e0e0e0'
              }
            }}
            onClick={handleDeleteAll} //    handleDeleteAll  
          >
             
          </Button>
        )}
      </NameContainer>
      <SearchWordsContainer>
        {searchWords.length > 0 ? (
          searchWords.map((word, index) => (
            <SearchWord key={index} searchWord={word} showClearIcon={true} /> // showClearIcon prop 
          ))
        ) : (
          <NoSearchWordsText>
               .
          </NoSearchWordsText>
        )}
      </SearchWordsContainer>
    </RecentSearchesContainer>
  );
};

export default RecentSearches;
