import React, { useState, useEffect } from 'react';
import { IconButton } from '@mui/material';
import BookmarkBorderOutlinedIcon from '@mui/icons-material/BookmarkBorderOutlined';
import BookmarkOutlinedIcon from '@mui/icons-material/BookmarkOutlined';
import axios from 'axios';

interface ProductBookmarkProps {
  productName: number;
}

const ProductBookmark: React.FC<ProductBookmarkProps> = ({ productName }) => {
  const [bookmarked, setBookmarked] = useState<boolean>(false);
  const userId = localStorage.getItem('userId');

  useEffect(() => {
    const fetchBookmarkStatus = async () => {
      try {
        const response = await axios.get(
          `/api/v1/users/${userId}/bookmarks/${productName}`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('jwtToken')}`
            }
          }
        );
        setBookmarked(response.data.bookmarked);
      } catch (error) {
        console.error('Error fetching bookmark status:', error);
      }
    };

    if (userId) {
      fetchBookmarkStatus();
    }
  }, [productName, userId]);

  const handleBookmarkClick = async () => {
    try {
      if (bookmarked) {
        await axios.delete(`/api/v1/users/${userId}/bookmarks/${productName}`, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('jwtToken')}`
          }
        });
        setBookmarked(false);
        alert(' .');
      } else {
        await axios.post(
          `/api/v1/users/${userId}/bookmarks`,
          {
            productName
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('jwtToken')}`
            }
          }
        );
        setBookmarked(true);
        alert(' .');
      }
    } catch (error) {
      console.error('Error handling bookmark:', error);
      alert(' .');
    }
  };

  return (
    <IconButton onClick={handleBookmarkClick}>
      {bookmarked ? <BookmarkOutlinedIcon /> : <BookmarkBorderOutlinedIcon />}
    </IconButton>
  );
};

export default ProductBookmark;
