import React from 'react';
import { Container, Header, Section, Description, KakaoButton } from './styles';
import { useNavigate } from 'react-router-dom';

const SignInPage: React.FC = () => {
  const navigate = useNavigate();

  //   URL .
  const KAKAO_AUTH_URL = `https://kauth.kakao.com/oauth/authorize?client_id=${import.meta.env.VITE_KAKAO_API_KEY}&redirect_uri=${encodeURIComponent(import.meta.env.VITE_SERVER_URI)}/auth/kakao/callback&response_type=code`;

  //      
  if (
    !import.meta.env.VITE_KAKAO_API_KEY ||
    !import.meta.env.VITE_SERVER_URI
  ) {
    console.error('Kakao API Key or Redirect URI is missing.');
    return (
      <Container>
        <Header>
          <img src="/assets/logo.png" alt="Slimdealz logo" />
        </Header>

        <Section>
          <Description>
                . <br />
               .
          </Description>
        </Section>
      </Container>
    );
  }

  const handleLogin = () => {
    window.location.href = KAKAO_AUTH_URL;
    // navigate('/signUp');
  };

  return (
    <Container>
      <Header>
        <img src="/assets/logo.png" alt="Slimdealz logo" />
      </Header>

      <Section>
        <Description>
            <br />
            .
        </Description>
        <KakaoButton className="kakao-login" onClick={handleLogin}>
          <span role="img" aria-label="kakao-logo">
            
          </span>{' '}
           
        </KakaoButton>
      </Section>
    </Container>
  );
};

export default SignInPage;