import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-cards';
import { EffectCards } from 'swiper/modules';
import {
  SwiperSlideStyled,
  ItemDetails,
  ItemImage,
  ItemName,
  OriginalPrice,
  DiscountRate,
  SalePrice,
  StyledSwiperContainer
} from './styles';
import { Title } from '../productSlider/styles';
import { useNavigate } from 'react-router-dom';
import { getNumberWithComma } from '@/components/utils/conversion';

interface ThirdSliderProps {
  items: {
    id: number;
    name: string;
    image: string;
    originalPrice: number;
    salePrice: number;
    discountRate: number;
  }[];
  title: string;
}

const ThirdSlider: React.FC<ThirdSliderProps> = ({ items, title }) => {
  const navigate = useNavigate();

  const handleTitleClick = () => {
    if (title === 'MY BOOKMARKS') {
      navigate(`/bookmark`);
    } else if (title === '') {
      navigate(`/lowest-price`);
    } else if (title === '') {
      navigate(`/recommended`);
    }
  };

  const handleProductClick = (productName: string) => {
    navigate(`/product/${productName}`);
  };

  return (
    <div
      style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column'
      }}
    >
      <Title onClick={handleTitleClick}>{title}</Title>
      <StyledSwiperContainer>
        <Swiper
          effect={'cards'}
          grabCursor={true}
          modules={[EffectCards]}
          style={{
            width: '280px',
            height: '350px'
          }}
        >
          {items.map((item) => (
            <SwiperSlide
              key={item.id}
              onClick={() => handleProductClick(item.name)}
            >
              <SwiperSlideStyled>
                <ItemImage src={item.image} alt={item.name} />
                <ItemDetails>
                  <ItemName>{item.name}</ItemName>
                  <OriginalPrice>
                    : {getNumberWithComma(item.originalPrice)}
                  </OriginalPrice>
                  <DiscountRate>: {item.discountRate}%</DiscountRate>
                  <SalePrice>
                    : {getNumberWithComma(item.salePrice)}
                  </SalePrice>
                </ItemDetails>
              </SwiperSlideStyled>
            </SwiperSlide>
          ))}
        </Swiper>
      </StyledSwiperContainer>
    </div>
  );
};

export default ThirdSlider;
