import React, { useEffect, useState } from 'react';
import {
  Container,
  MallItem,
  MallLogo,
  MallInfo,
  MallPrice,
  Coupon
} from './styles';
import { getNumberWithComma } from '@/components/utils/conversion';
import axios from 'axios';

interface ProductData {
  name: string;
  prices?: { setPrice: number }[];
}

interface TabsComponentProps {
  productData: ProductData;
}
const MallList: React.FC<TabsComponentProps> = ({ productData }) => {
  const { name: productName, prices } = productData; // productData   
  const [mallData, setMallData] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchMallData = async () => {
      try {
        const response = await axios.get(`/api/v1/vendor-list`, {
          params: { productName } //    
        });
        setMallData(response.data);
      } catch (err) {
        console.log('    .');
      } finally {
        setLoading(false);
      }
    };

    fetchMallData();
  }, [productName]);

  if (loading) return <div> ...</div>;

  return (
    <Container>
      {mallData.map((item) => (
        <MallItem key={productName}>
          {/* <MallLogo src={item.logo} alt={item.name} /> */}
          <MallInfo>
            <div>{productName}</div>
            <MallPrice>
              {prices && prices.length > 0
                ? `${getNumberWithComma(prices[0].setPrice)}`
                : ' '}
            </MallPrice>
          </MallInfo>
          {/* {item.coupon && <Coupon>{item.coupon}</Coupon>} */}
        </MallItem>
      ))}
    </Container>
  );
};

export default MallList;
