import React, { useState } from 'react';
import { IconButton } from '@mui/material';
import axios from 'axios';
import BookmarkBorderOutlinedIcon from '@mui/icons-material/BookmarkBorderOutlined';
import BookmarkOutlinedIcon from '@mui/icons-material/BookmarkOutlined';
import {
  Container,
  ImageContainer,
  InfoContainer,
  // BookmarkContainer,
  PriceContainer,
  PriceText,
  SmallText,
  // BookmarkCount,
  BookmarkCountWrapper
} from './styles';
import { getNumberWithComma } from '@/components/utils/conversion';

type Props = {
  id: number;
  //  image: string;
  name: string;
  price: number;
  // per100gPrice: string;
  shipping: string;
  // rating: number; //  :    
  // bookmarkCount: number; //  :    
};

const CategoryList = ({
  id,
  // image,
  name,
  price,
  // per100gPrice,
  shipping
  // rating,
  // bookmarkCount
}: Props) => {
  const [bookmarked, setBookmarked] = useState(false);
  // const [bookmarkCount, setBookmarkCount] = useState<number>(0);
  const userId = localStorage.getItem('userId'); //  ID  ( )

  const handleBookmarkClick = async () => {
    // setBookmarkCount((prevCount) =>
    //   bookmarked ? prevCount - 1 : prevCount + 1
    // );
    try {
      if (bookmarked) {
        //  
        await axios.delete(`/api/v1/users/${userId}/bookmarks/${id}`, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('jwtToken')}`
          }
        });
        setBookmarked(false);
        alert(' .');
      } else {
        //  
        await axios.post(
          `/api/v1/users/${userId}/bookmarks`,
          { userId, productName: name },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('jwtToken')}`
            }
          }
        );
        setBookmarked(true);
        alert(' .');
      }
    } catch (error: any) {
      if (error.response) {
        if (error.response.status === 400) {
          alert('  .');
        } else if (error.response.status === 401) {
          alert(' .');
        } else if (error.response.status === 500) {
          alert('  .');
        }
      } else {
        alert('  .');
      }
    }
  };

  return (
    <Container>
      <ImageContainer>
        <img
          //  src={image}
          alt={name}
          style={{ width: '100%', height: '100%', borderRadius: '10px' }}
        />
      </ImageContainer>
      <InfoContainer>
        <div style={{ fontSize: '16px', fontWeight: 'bold' }}>{name}</div>
        <PriceContainer>
          <PriceText>{getNumberWithComma(price)}</PriceText>
        </PriceContainer>
        {/* <SmallText>[100g  {per100gPrice}]</SmallText> */}
        <SmallText>{' : ' + shipping}</SmallText>

        <BookmarkCountWrapper>
          <IconButton
            onClick={(e) => {
              e.stopPropagation(); //      
              handleBookmarkClick();
            }}
            style={{ paddingLeft: '10px' }}
          >
            {bookmarked ? (
              <BookmarkOutlinedIcon />
            ) : (
              <BookmarkBorderOutlinedIcon />
            )}
          </IconButton>
          {/* <BookmarkCount>{bookmarkCount}</BookmarkCount> */}
        </BookmarkCountWrapper>
        {/* <BookmarkContainer onClick={(e) => e.stopPropagation()}>
          <Rating value={rating} readOnly />
        </BookmarkContainer> */}
      </InfoContainer>
    </Container>
  );
};

export default CategoryList;
